.blockNumbers {
    width: 100px;
    height: 80px;
    background-color: lightyellow;
    border: 3px solid black;
}

.blockWithNumbers {
    text-align: center;
    margin: 150px auto;
    display: flex;
    gap:2px;
    flex-wrap: wrap;
    max-width: 370px;
    font-size: 37px;
}

.blockNumbers:hover {
    background-color: powderblue;
    opacity: 0.8;
    cursor: pointer;
}

.blockSigns {

    width: 77.2px;
